/**
 * validator
 */


import Validator from './validator';
import * as Format from '../format';

/*
 * maxLine, minLine
 * allowComment
 */
export default class MultiLineCompositeValidator extends Validator {

    /**
     * @cfg {Number} minLine (optional) 
     */

    /**
     * @cfg {String} minLineText (optional) 
     */
    minLineText = '{0}';

    /**
     * @cfg {Number} maxLine (optional) 
     */

    /**
     * @cfg {String} maxLineText (optional) 
     */
    maxLineText = '{0}';

    /**
     * @cfg {Boolean} allowComment (optional) #xxxfalse
     */

    /**
     * @cfg {String} commentPerfix (optional) #
     */
    commentPerfix = '#';

    /**
     * @cfg {String} lineInvalidText (optional) 
     */
    lineInvalidText = '{0}{2}{1}';

    /**
     * @cfg {Boolean} formalizefalse
     * formalize
     */
    enableFormalize = false;

    /**
     * @cfg {String} formalLineBreak (optional) \n
     */
    formalLineBreak = '\n';

    /**
     * 
     * @param {Number} line N
     * @return {Object} result text, comment(commentallowCommentnull)
     */
    _parseLine (line) {
        let text = line;
        let comment = null;
        let index;
        if (line && this.allowComment) {
            index = line.indexOf(this.commentPerfix);
            if (index >= 0) {
                text = line.slice(0, index);
                comment = line.slice(index);
            }
        }
        text = Format.trim(text);
        return {
            text: text,
            comment: comment
        };
    }
    _parseLines (value = '', fn, scope) {
        let lines = value.split(/\r?\n/);
        let i;
        let result;
        let results = [];
        for (i = 0; i < lines.length; i++) {
            result = this._parseLine(lines[i]);
            result.index = i;
            if (fn && fn.call(scope, result, i) !== false) {
                results.push(result);
            }
        }
        return results;
    }
    verify (value) {
        let actualLines = 0;
        let results = this._parseLines(value, function (result/*, index*/) {
            if (result.text) {
                actualLines++;
            } else {
                return false;
            }
        }, this);

        // 
        if (this.minLine && actualLines < this.minLine) {
            return Format.formatString(this.minLineText, this.minLine);
        }
        if (this.maxLine && actualLines > this.maxLine) {
            return Format.formatString(this.maxLineText, this.maxLine);
        }

        let i;
        let result;
        let text;
        let ret;
        for (i = 0; i < results.length; i++) {
            result = results[i];
            text = result.text;
            ret = this.validator.verify(text);
            if (typeof ret === 'string') {
                return Format.formatString(
                    this.lineInvalidText,
                    result.index + 1,
                    Format.htmlEncode(ret),
                    Format.htmlEncode(text)
                );
            }
        }
        return true;
    }
    formalize (value) {
        let strs = [];
        let formalized = false;
        let me = this;
        this._parseLines(value, function (result/*, index*/) {
            let str;
            let comment;
            let ret;
            str = result.text || '';
            comment = result.comment || '';
            if (str) {
                ret = me.validator.formalize(str);
                if (ret !== null) {
                    formalized = true;
                    str = ret;
                }
            } else {
                str = '';
            }
            str += comment;

            // 
            if (str) {
                strs.push(str);
            }
        }, this);
        if (formalized || this.enableFormalize) {
            return strs.join(this.formalLineBreak);
        }
        return null;
    }
}



