import Vue from 'vue';
import logger from '../util/logger';
import Lang from './lang.vue';

import en from './en';
import zh from './zh_cn';

const LANGUAGE = {
    'zh-CN': zh,
    en
};

/**
 * 
 */
let cur = 'zh-CN';

/**
 * _ 
 * @param {String} str    _('{0}{1}', 'replace0', 'replace1')
 * @param {String} args   {N} 
 * @returns {String}      
 */
function _ (str, ...args) {
    let langMap = LANGUAGE[cur];
    if (langMap && langMap.hasOwnProperty(str)) {
        str = langMap[str];
    }

    /**
     *  {#mark#} 
     * 
     * EnabledEnable
     * 
     * {#adj#} -> Enabled
     * {#verb#} -> Enable
     * 
     */
    return str.replace(/\{(\d+|#\w+#)\}/g, function (m, i) {
        i = parseInt(i, 10);
        if (isNaN(i)) {
            return '';
        }
        if (i >= 0 && i < args.length) {
            return args[i];
        }
        return m;
    });
}


/**
 *  _()
 * @param {Object} Vue Vue
 * @param {Object} opt i18n
 * @return {Boolean} 
 */
function initI18n (Vue, opt) {
    cur = opt.lang;
    if (typeof window !== 'undefined') {
        let old;
        if (window._) {
            logger.log('function _ () {} found...');
            old = window._;
            window._ = function (str, ...args) {
                return _(old.apply(null, arguments), ...args);
            };
        } else {
            window._ = _;
        }
        return true;
    }
    return false;
}
initI18n(Vue, { lang: 'zh-CN' });

export default {
    install (Vue, opt = { lang: 'zh-CN' }) {
        Vue.component('lang', Lang);
        cur = opt.lang;
    }
};




