import { apply } from '../apply';

export const VALIDATIONS = new Map();

export default class Validator {

    constructor (cfg) {
        let type;
        cfg = cfg || {};

        // Validatortype
        type = cfg.type;
        if (type && VALIDATIONS[type] && this.constructor === Validator.prototype.constructor) {
            delete cfg.type;
            return new VALIDATIONS[type](cfg);
        }
        apply(this, cfg);
    }

    /**
     * 
     * @param {String} value            
     * @return {Boolean/String} result  stringtrue
     */
    verify () {
        return true;
    }

    /**
     * 
     * @param {String} value            
     * @return {String} formalValue     null
     */
    formalize () {
        return null;
    }
}



