import uuid from 'src/util/uuid';
import logger from 'src/util/logger';
import { cascadeComponent } from 'src/util/walk';

export default {

    //beforeCreate () {
    //    logger.log(`component before create...${this.id}`);
    //},
    //created () {
    //    logger.log(`component created...${this.id}`);
    //},
    //
    //beforeMount () {
    //    logger.log(`component before mount...${this.id}`);
    //},

    mounted () {
        this.$isMounted = true;
    },

    //
    //beforeUpdate () {
    //    logger.log(`component before update...${this.id}`);
    //},
    //updated () {
    //    logger.log(`component mounted...${this.id}`);
    //},
    //
    //
    //beforeDestroy () {
    //    logger.log(`component beforeDestroy...${this.id}`);
    //},
    //
    //destroyed () {
    //    logger.log(`component destroyed...${this.id}`);
    //},

    data () {
        return {
            id: uuid(),
            width: this.defaultWidth,
            height: this.defaultHeight,
            disabled: this.defaultDisabled,
            hidden: this.defaultHidden
        };
    },

    props: {
        cls: {},
        defaultHidden: {
            type: Boolean,
            'default': false
        },
        defaultDisabled: Boolean,
        defaultWidth: [Number, String],
        defaultHeight: Number
    },

    computed: {

    },


    methods: {

        /**
         * id
         * @return {String} id
         */
        getId () {
            return this.id;
        },

        /**
         * 
         * @param {Boolean} silent 
         */
        disable (silent = false) {
            if (this.disabled) {
                return;
            }
            this.disabled = true;
            if (!silent) {
                this.$emit('disable', this);
            }
        },

        /**
         * 
         * @param {Boolean} silent 
         */
        enable (silent = false) {
            if (!this.disabled) {
                return;
            }
            this.disabled = false;
            if (!silent) {
                this.$emit('enable', this);
            }
        },

        /**
         * 
         * @param {Boolean} silent 
         */
        show (silent) {
            this.hidden = false;
            logger.log('show component...');
            if (!silent) {
                this.$emit('show', this);
            }
        },

        /**
         * 
         * @param {Boolean} silent 
         */
        hide (silent) {
            this.hidden = true;
            logger.log('hide component...');
            if (!silent) {
                this.$emit('hide', this);
            }
        },

        /**
         * dom
         * @return {Boolean} 
         */
        isVisibility () {
            let visibility = true;
            let p = this;
            while (p) {
                if (p.hidden) {
                    visibility = false;
                    break;
                }
                p = p.$parent;
            }
            return visibility;
        }
    },

    watch: {

        /**
         * 
         */
        hidden () {
            let me = this;
            this.$nextTick(function () {
                cascadeComponent(me, (child) => {
                    return child.$emit('parentvisibilitychange', me);
                });
            });
        },

        /**
         * 
         * @param {Boolean} value 
         */
        defaultHidden (value) {
            this.hidden = value;
        },

        /**
         * 
         * @param {Boolean} value 
         */
        defaultDisabled (value) {
            this.disabled = value;
        },

        /**
         * 
         * @param {Boolean} value 
         */
        defaultWidth (value) {
            this.width = value;
        },

        /**
         * 
         * @param {Boolean} value 
         */
        defaultHeight (value) {
            this.height = value;
        }
    }
};



