//import uuid from 'src/util/uuid';
//import logger from 'src/util/logger';
//import { updateJsonField } from 'src/vuex/actions';
import { decodeDate } from 'src/util/format';

const INVALID_CLASS = 'invalid';

export default {
    created () {

        // 
        let parent = this.$parent;
        while (parent) {
            if (parent.isJsonForm) {
                this.$jsonForm = parent;
                break;
            }
            parent = parent.$parent;
        }
    },

    mounted () {

    },

    data () {
        return {
            readonly: this.defaultReadonly,
            invalidClass: '',
            invalidText: ''
        };
    },

    props: {

        // v-modelgetDomValue()
        value: {},

        name: String,
        fieldTitle: String,
        type: String,      // text, trigger (search ...)

        defaultReadonly: Boolean,

        validator: Function,

        // 
        invalidType: { type: String, 'default': 'error' },
        invalidPosition: { type: String, 'default': 'bottom-start' }

        // hintText 
        // hintText: '',

    },

    methods: {

        /**
         *  sf-form true
         * @returns {Boolean} jsonField
         */
        isJsonField () {
            return true;
        },

        /**
         * sf-form 
         * @returns {String} 
         */
        getName () {
            return this.name;
        },

        getJsonValue () {
            return this.processValue(this.value);
        },

        setJsonValue (value) {
            this.value = value;
        },

        setReadOnly (readOnly) {
            this.readonly = readOnly;
        },


        isValid (preventMark) {
            return this.disabled || this.validateValue(this.getJsonValue(), preventMark);
        },

        validate () {
            if (this.disabled || this.validateValue(this.getJsonValue())) {
                this.clearInvalid();
                return true;
            }
            return false;
        },

        /**
         * 
         * private
         * @param {String|Number} value         value
         * @param {Boolean} preventMark         markInvalid
         * @returns {Boolean}                   
         */
        validateValue (value, preventMark) {
            let error = this.getErrors(value)[0];
            if (typeof error === 'undefined') {
                return true;
            } else if (preventMark !== true) {
                this.markInvalid(error);
            }
            return false;
        },

        /**
         * 
         * 
         * @returns {Array} 
         */
        getErrors () {
            return [];
        },

        /**
         * error
         * @param {String} msg 
         */
        markInvalid (msg) {
            this.invalidClass = INVALID_CLASS;
            this.invalidText = msg;
        },

        /**
         * 
         */
        clearInvalid () {
            this.invalidClass = '';
            this.invalidText = '';
        },

        /**
         * 
         * @param {*} value 
         */
        onInput (value) {
            this.$emit('input', this.processValue(value));
            this.validate();
        },

        /**
         * typedate number
         * @param {String} value 
         * @returns {*}          
         */
        processValue (value) {
            let ret = value;
            if (this.type === 'date') {
                if (value instanceof Date) {
                    ret = value;
                } else {
                    ret = decodeDate(value);
                }
            } else if (this.type === 'number') {
                if (value === null || typeof value === 'undefined' || value === '') {
                    return;
                }

                ret = Number(value);
                if (isNaN(ret)) {
                    return value;
                }
            } else {
                ret = value;
            }
            return ret;
        },

        getDomValue () {

            // input
            return this.value;
        }

    },

    //vuex: {
    //    getters: {
    //        value (state) {
    //            return state.form.message;
    //        }
    //    },
    //    actions: {
    //        updateJsonField: ({ dispatch }, value, $jsonForm) => {
    //            dispatch('form/UPDATE', value, $jsonForm);
    //        }
    //    }
    //},

    computed: {

    },

    watch: {
        value (newValue) {
            this.onInput(newValue);
        }
    }
};



