<template>
    <button class="btn"
            :id="id"
            :class="[cls, selected ? 'btn-selected' : '']"
            type="button"
            @click="onClick"
            :disabled="disabled"
            :style="{ width:width + 'px', height: height + 'px' }"
            v-show="!hidden"
    >
        <i v-if="iconCls" class="fa" :class="iconCls"></i>
        <slot></slot>
    </button>
</template>
<style>
    @import "./button.css";
</style>
<script>
    /**
     * buttom
     */

    import Component from 'src/widgets/component';

    export default {

        props: {

            /**
             * @cfg 
             */
            iconCls: {
                type: String
            },

            /**
             * @cfg grid_mgraction
             */
            actionName: {
                type: String
            },

            /**
             * @cfg 
             */
            defaultSelected: {
                type: Boolean,
                'default': false
            },

            /**
             * @cfg btn-selected
             */
            selectAble: {
                type: Boolean,
                'default': false
            },

            /**
             * @cfg grid_mgrtrue|false
             */
            selectMust: {}
        },

        data () {
            return {
                selected: this.defaultSelected
            };
        },

        mixins: [
            Component
        ],

        methods: {

            /**
             * 
             * @param {Event} event 
             */
            onClick (event) {
                if (this.selectAble) {
                    this.selected = !this.selected;
                }

                this.$emit('click', event, this);

                // mgr
                if (this.actionName) {
                    this.$emit('action', event, this);
                }
            }
        },

        watch: {
            defaultSelected (value) {
                this.selected = value;
            }
        }
    };
</script>



