/**
 * validator
 */


import Validator from './validator';
import * as Format from '../format';

/*
 * maxCount, minCount
 * allowComment
 */
export default class JoinedCompositeValidator extends Validator {

    /**
     * @cfg {Number} minCount (optional) 
     */
    /**
     * @cfg {String} minCountText (optional) 
     */
    minCountText = '{0}';

    /**
     * @cfg {Number} maxCount (optional) 
     */
    /**
     * @cfg {String} maxCounteText (optional) 
     */
    maxCountText = '{0}';

    /**
     * @cfg {Boolean} allowComment (optional) #xxxfalse
     */
    /**
     * @cfg {String/Regex} splitter (optional)  ,formalSplitter
     */
    splitter = ',';

    /**
     * @cfg {String} formalSplitter (optional) ,splitter
     */
    formalSplitter = ',';

    /**
     * @cfg {Boolean} ignoreBlank (optional) true
     */
    ignoreBlank = true;

    /**
     * @cfg {String} partInvalidText (optional) 
     */
    partInvalidText = '{0}{2}{1}';

    /**
     * @cfg {Boolean} formalizeignoreBlank/a, b, ca,b,cfalse
     */
    enableFormalize = false;

    _parseParts (value) {
        if (!value) {
            return []; // 
        }
        let parts = value.split(this.splitter);
        let i;
        let result;
        let ignoreBlank = this.ignoreBlank;
        let results = [];
        for (i = 0; i < parts.length; i++) {
            result = parts[i];
            if (ignoreBlank) {
                result = Format.trim(result);
            }
            results.push(result);
        }
        return results;
    }
    verify (value) {
        let parts = this._parseParts(value);
        let count = parts.length;

        // 
        if (this.minCount && count < this.minCount) {
            return Format.formatString(this.minCountText, this.minCount);
        }
        if (this.maxCount && count > this.maxCount) {
            return Format.formatString(this.maxCountText, this.maxCount);
        }

        let i;
        let text;
        let ret;
        for (i = 0; i < parts.length; i++) {
            text = parts[i];
            ret = this.validator.verify(text);
            if (typeof ret === 'string') {
                return Format.formatString(this.partInvalidText, i + 1, ret, Format.htmlEncode(text));
            }
        }
        return true;
    }
    formalize (value) {
        let parts = this._parseParts(value);
        let i;
        let str;
        let ret;
        let ignoreBlank = this.ignoreBlank;
        let formalized = false;
        let strs = [];
        for (i = 0; i < parts.length; i++) {
            str = parts[i];
            if (str) {
                ret = this.validator.formalize(str);
                if (ret !== null) {
                    formalized = true;
                    str = ret;
                }
            }
            strs.push(str);
        }
        if (formalized || this.enableFormalize) {

            // 
            return strs.join(this.formalSplitter + (ignoreBlank ? ' ' : ''));
        }
        return null;
    }
}



