const START = 0x4E00;
const END = 0x9FA5;

/* eslint-disable max-len */
const DB = '';

/* eslint-enable max-len */

function getOrderedUnicode (char) {
    let originalUnicode = char.charCodeAt();
    if (originalUnicode >= START && originalUnicode <= END) {
        let index = DB.indexOf(char);
        if (index > -1) {
            return index + START;
        }
    }
    return originalUnicode;
}

function compare (a, b) {
    if (a === b) {
        return 0;
    }
    if (typeof a === 'number' && typeof b === 'number') {
        return a - b;
    }
    a = String(a);
    b = String(b);

    if (!a.length) {
        return 1;
    }
    if (!b.length) {
        return -1;
    }
    let count = a.length > b.length ? b.length : a.length;
    for (let i = 0; i < count; i++) {
        let au = getOrderedUnicode(a[i]);
        let bu = getOrderedUnicode(b[i]);

        if (au > bu) {
            return 1;
        } else if (au < bu) {
            return -1;
        }
    }

    return a.length > b.length ? 1 : -1;
}

export {
    compare
};


