/**
 * RFC 1035
 * RFC 11232LabelLabel
 * IP
 * {@link SF.validations.GlobalName}
 * @class Domain
 * @extends Validator
 */

import Validator from '../validation/validator';

const REGEX = /^([a-z\d]([-_a-z\d]{0,61}[a-z\d])?\.)*[a-z]([-a-z\d]{0,61}[a-z\d])?\.?$/i;

export default class Domain extends Validator {

    verify (value) {
        if (REGEX.test(value)) {
            return true;
        }
        return _('');
    }

    formalize (value) {
        let str;
        if (value && value.toLowerCase) {
            str = value.toLowerCase();
            if (str !== value) {
                return str;
            }
        }
        return null;
    }
};



