/**
 * 
 */


import Validator from './validator';
import { formatString } from 'src/util/format';

/**
 *   <br />
 * username:password^(\w+):(\w+)$blocks <br />
 * [{ name: "", validator: usernameValidator}, { name: "", validator: passwordValidator}]
 * @class SimpleCompositeValidator
 * @extends Validator
 */
export default class SimpleCompositeValidator extends Validator {

    /**
     * @cfg {RegExp} regex {@link #blocks} <br />
     * /^(\w+)#(\w+)$//^(\w+)(#)(\w+)$/ <br />
     * validatorindex13 <br />
     * formalizegroups
     */
    regex = /(.*)/i;

    /**
     * @cfg {String} matchInvalidText 
     */
    matchInvalidText = '';

    /**
     * @cfg {String} invalidText 01{0},{1}
     * 
     */
    invalidText = '{0}{1}';

    /**
     * 
     * @param {Object} cfg   
     * @cfg {Array} blocks 
     * 3nameindexvalidatorname
     * index{@link #regex}validator
     */
    constructor (cfg) {
        super(cfg);
        Object.assign(this, cfg || {});
        let me = this;
        if (!this.blocks) {
            this.blocks = [];
        }

        // blockMap
        this.blockMap = {};
        this.blocks.forEach(function (block, index) {
            me.blockMap[block.index || index + 1] = block;
        }, this);
    }
    verify (value) {
        let matchResult;
        let result;
        let i;
        let block;
        let blockTexts;
        if (value && value.match) {
            matchResult = value.match(this.regex);
            if (matchResult) {

                // blocks
                for (i = 1; i < matchResult.length; i++) {

                    //  (.+)?
                    if (matchResult[i] && this.blockMap.hasOwnProperty(i)) {
                        block = this.blockMap[i];
                        if (!block) {
                            throw formatString('block {0} is empty!', i);
                        }
                        result = block.validator.verify(matchResult[i]);
                        if (typeof result === 'string') {
                            return formatString(this.invalidText, block.name, result);
                        }
                    }
                }

                // 
                blockTexts = [];
                for (i = 0; i < this.blocks.length; i++) {
                    block = this.blocks[i];
                    blockTexts.push(matchResult[block.index]);
                }
                result = this.externalVerify.apply(this, blockTexts);
                if (typeof result === 'string') {
                    return result;
                }
                return true;
            }
        }
        return this.matchInvalidText;
    }

    /**
     * 
     * @param {String} arg1  block
     * @return {Boolean/String} result true
     */
    externalVerify () {
        return true;
    }

    formalize (value) {
        let me = this;
        let strs = [];
        let changed = false;
        const CNT = 2;
        if (value && value.replace) {
            value.replace(this.regex, function (/*text*/) {
                let i;
                let block;
                let groupText;
                let result;
                for (i = 1; i < arguments.length - CNT; i++) {
                    groupText = arguments[i];
                    strs.push(groupText);
                    if (me.blockMap.hasOwnProperty(i)) {
                        block = me.blockMap[i];
                        result = block.validator.formalize(groupText);
                        if (result) {
                            changed = true;
                            strs[i - 1] = result;
                        }
                    }
                }
            });
            if (changed) {
                return strs.join('');
            }
        }
        return null;
    }
}



