/**
 * @RFC 2822
 * {@link SF.validations.StrictEmailName}
 * @class EmailName
 * @extends Validator
 */

import Validator from '../validation/validator';

/* eslint-disable max-len */
const REGEX = /^(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")$/i;

/* eslint-enable max-len */

export default class EmailName extends Validator {

    /**
     * 
     * @override
     * @param {String} value     
     * @returns {Boolean|String} true
     */
    verify (value) {
        if (REGEX.test(value)) {
            return true;
        }
        return _('');
    }
};



